-box;\n  background-color: #ffffeb;\n}\n.im .im-wrapper .main_container .panel_footer {\n  border: 1px solid #cbbb7a;\n  background: url(\"./images/nav_tab_item_bg.png\") 50% 0 repeat-x;\n}\n.im .im-wrapper .chat_wrapper {\n  position: relative;\n  width: 700px;\n  height: 100%;\n  float: left;\n  margin-left: 14px;\n}\n.im .im-wrapper .main-panel,\n.im .im-wrapper .chat-panel {\n  height: 80%;\n  top: 5%;\n  border-radius: 4px;\n}\n.im .im-wrapper .main-panel {\n  position: relative;\n}\n.im .im-wrapper .chat-panel {\n  position: absolute;\n}\n.im .im-wrapper .main-panel {\n  display: block;\n  width: 280px;\n  -webkit-transition: none !important;\n  transition: none !important;\n  -webkit-transform: none !important;\n          transform: none !important;\n}\n.im .im-wrapper .main-panel .side_bar {\n  width: 260px;\n  height: 100%;\n  background: url(\"./images/side_bar-bg.png\") 0 0 repeat;\n  border-radius: 4px 0 0 4px;\n  -webkit-transition: -webkit-transform 0.5s;\n  transition: -webkit-transform 0.5s;\n  transition: transform 0.5s;\n  transition: transform 0.5s, -webkit-transform 0.5s;\n  -webkit-transform: translate3d(0, 0, 0);\n          transform: translate3d(0, 0, 0);\n}\n.im .im-wrapper .main-panel .side_bar.show {\n  -webkit-transform: translate3d(-256px, 0, 0);\n          transform: translate3d(-256px, 0, 0);\n}\n.im .im-wrapper .main-panel .side_bar .side_bar_close {\n  position: absolute;\n  top: 0px;\n  left: 0px;\n  border-radius-bottomright: 0;\n}\n.im .im-wrapper .main-panel .side_bar .user_info {\n  text-align: center;\n  padding: 21px 0 13px;\n}\n.im .im-wrapper .main-panel .side_bar .user_info .avatar_wrapper {\n  display: inline-block;\n  border: 1px solid #fefeea;\n  border-radius: 50%;\n  padding: 13px;\n}\n.im .im-wrapper .main-panel .side_bar .user_info .avatar_wrapper .avatar {\n  width: 81px;\n  height: 81px;\n  border-radius: 50%;\n}\n.im .im-wrapper .main-panel .side_bar .user_info .user_name,\n.im .im-wrapper .main-panel .side_bar .us